import Parser from './js/Parser';
import ResultsBuilder from './js/ResultsBuilder';

onmessage = ({ data }) => {
  const reader = new FileReader();
  reader.onload = (e) => {
    const scratchFile = e.target.result.split('\r\n');
    const parser = new Parser(scratchFile);
    parser.process();

    const zones = ResultsBuilder.Compile(parser.getAllData());
    const groundFloor = zones.filter(zone => zone.onGroundFloor);
    const otherFloor = zones.filter(zone => !zone.onGroundFloor);
    const sorted = [...groundFloor, ...otherFloor];
    const results = ResultsBuilder.Build(sorted, data.reference);

    postMessage({ results });
  };

  reader.abort();
  reader.readAsText(data.file);
};



