import HorizontalShadingScheme from '../HorizontalShadingScheme';
import WindowConstruction from '../WindowConstruction';
import ZoneWindow from '../ZoneWindow';
import CeilingHeight from '../CeilingHeight';
import ZoneDetail from '../ZoneDetail';
import FloorConstruction from '../FloorConstruction';
import ZoneFloor from "../ZoneFloor";

/**
 * A class to compare a data string and indicate what type of data it looks like
 */
class LooksLike {

  /**
   * Test if the data line looks like a horizontal shading scheme
   *
   * @param {string} lineData The data line to test
   *
   * @returns {boolean}
   */
  static ShadingScheme(lineData) {
    return LooksLike.Test(lineData, HorizontalShadingScheme.Regex);
  }

  /**
   * Test if the data line looks like a window construction
   *
   * @param {string} lineData The data line to test
   *
   * @returns {boolean}
   */
  static WindowConstruction(lineData) {
    return LooksLike.Test(lineData, WindowConstruction.Regex);
  }

  /**
   * Test if the data line looks like a zone window
   *
   * @param {string} lineData The data line to test
   *
   * @returns {boolean}
   */
  static ZoneWindow(lineData) {
    return LooksLike.Test(lineData, ZoneWindow.Regex);
  }

  /**
   * Test if the data line looks like a ceiling height line
   *
   * @param {string} lineData The data line to test
   *
   * @returns {boolean}
   */
  static CeilingHeight(lineData) {
    return LooksLike.Test(lineData, CeilingHeight.Regex);
  }

  /**
   * Test if the data line looks like a zone detail line
   *
   * @param {string} lineData The data line to test
   *
   * @returns {boolean}
   */
  static ZoneDetail(lineData) {
    return LooksLike.Test(lineData, ZoneDetail.Regex);
  }

  /**
   * Test if the data line looks like a floor construction line
   *
   * @param {string} lineData The data line to test
   *
   * @returns {boolean}
   */
  static FloorConstruction(lineData) {
    return LooksLike.Test(lineData, FloorConstruction.Regex);
  }

  /**
   * Test if the data line looks like a zone floor line
   *
   * @param {string} lineData The data line to test
   *
   * @returns {boolean}
   */
  static ZoneFloor(lineData) {
    return LooksLike.Test(lineData, ZoneFloor.Regex);
  }

  /**
   * Run the regex test against the data
   *
   * @param {string} data  The data to test
   * @param {string} regex The regex pattern to test with
   *
   * @returns {boolean}
   */
  static Test(data, regex) {
    const regexp = new RegExp(regex);
    return regexp.test(data);
  }

  /**
   * Determine what the data line looks like
   *
   * @param {string} data The data line to test
   *
   * @returns {string|boolean}
   */
  static ReverseLookup(data) {
    if (LooksLike.ShadingScheme(data)) {
      return 'HorizontalShadingScheme';
    }

    if (LooksLike.CeilingHeight(data)) {
      return 'CeilingHeight';
    }

    if (LooksLike.WindowConstruction(data)) {
      return 'WindowConstruction';
    }

    if (LooksLike.ZoneWindow(data)) {
      return 'ZoneWindow';
    }

    if (LooksLike.ZoneDetail(data)) {
      return 'ZoneDetail';
    }

    if (LooksLike.FloorConstruction(data)) {
      return 'FloorConstruction';
    }

    if (LooksLike.ZoneFloor(data)) {
      return 'ZoneFloor';
    }

    return false;
  }

}

export default LooksLike;



