/**
 * Class CeilingHeight
 */
class CeilingHeight {

  static Regex = '^ 3(.{3})700(.{6})(.{6})(.{6})(.{6})';

  zoneId = '';
  height = '';

  constructor(lineData) {
    const regex = new RegExp(CeilingHeight.Regex);
    const data = regex.exec(lineData);

    this.zoneId = data[1].trim();
    this.height = data[3].trim();
  }

  getZoneId() {
    return this.zoneId;
  }

  getHeight() {
    return this.height;
  }

  toObject() {
    return {
      zoneId: this.getZoneId(),
      height: this.getHeight()
    };
  }

  static Build(data) {
    return new CeilingHeight(data);
  }

}

export default CeilingHeight;



