/**
 * A class to handle parsing an entire scratch file
 */
import ScratchFileLine from './ScratchFileLine';
import LooksLike from './utils/LooksLike';
import WindowConstruction from './WindowConstruction';
import ZoneWindow from './ZoneWindow';
import HorizontalShadingScheme from './HorizontalShadingScheme';
import CeilingHeight from './CeilingHeight';
import ZoneDetail from './ZoneDetail';
import FloorConstruction from './FloorConstruction';
import ZoneFloor from './ZoneFloor';

class Parser {

  windowConstructions = [];
  zoneWindows = [];
  zoneDetails = [];
  shadingSchemes = [];
  ceilingHeights = [];

  /**
   * Constructor
   *
   * @param {array} file A scratchfile array
   */
  constructor(file) {
    this.file = file;
  }

  process() {
    const dataLines = this.getDataLines();

    // Filter the arrays to only include the related data, then build the data line into the appropriate class
    this.windowConstructions = dataLines
      .filter(line => LooksLike.ReverseLookup(line) === 'WindowConstruction')
      .map(window => WindowConstruction.Build(window));

    this.zoneWindows = dataLines
      .filter(line => LooksLike.ReverseLookup(line) === 'ZoneWindow')
      .map(zoneWindow => ZoneWindow.Build(zoneWindow));

    this.shadingSchemes = dataLines
      .filter(line => LooksLike.ReverseLookup(line) === 'HorizontalShadingScheme')
      .map(shading => HorizontalShadingScheme.Build(shading));

    this.ceilingHeights = dataLines
      .filter(line => LooksLike.ReverseLookup(line) === 'CeilingHeight')
      .map(ceilingHeight => CeilingHeight.Build(ceilingHeight));

    this.zoneDetails = dataLines
      .filter(line => LooksLike.ReverseLookup(line) === 'ZoneDetail')
      .map(zoneDetail => ZoneDetail.Build(zoneDetail));

    this.floorConstructions = dataLines
      .filter(line => LooksLike.ReverseLookup(line) === 'FloorConstruction')
      .map(floorConstruction => FloorConstruction.Build(floorConstruction));

    this.zoneFloors = dataLines
      .filter(line => LooksLike.ReverseLookup(line) === 'ZoneFloor')
      .map(zoneFloor => ZoneFloor.Build(zoneFloor));
  }

  /**
   * Get the lines of data that match a type we're interested in
   *
   * @returns {array}
   */
  getDataLines() {
    return this.file.filter(line => ScratchFileLine.Build(line) !== false);
  }

  getWindowConstructions() {
    return this.windowConstructions;
  }

  getZoneWindows() {
    return this.zoneWindows;
  }

  getShading() {
    return this.shadingSchemes;
  }

  getCeilingHeights() {
    return this.ceilingHeights;
  }

  getZoneDetails() {
    return this.zoneDetails;
  }

  getFloorConstructions() {
    return this.floorConstructions;
  }

  getZoneFloors() {
    return this.zoneFloors;
  }

  /**
   * Return all data in an object that can be deconstructed
   *
   * @returns {Object}
   */
  getAllData() {
    return {
      windowConstructions: this.getWindowConstructions(),
      zoneWindows: this.getZoneWindows(),
      shading: this.getShading(),
      ceilingHeights: this.getCeilingHeights(),
      zoneDetails: this.getZoneDetails(),
      floorConstructions: this.getFloorConstructions(),
      zoneFloors: this.getZoneFloors()
    };
  }

}

export default Parser;



