import CeilingHeight from './CeilingHeight';
import HorizontalShadingScheme from './HorizontalShadingScheme';
import LooksLike from './utils/LooksLike';
import WindowConstruction from './WindowConstruction';
import ZoneWindow from './ZoneWindow';
import ZoneDetail from './ZoneDetail';
import FloorConstruction from './FloorConstruction';
import ZoneFloor from './ZoneFloor';

/**
 * Class ScratchFileLine
 */
class ScratchFileLine {

  /**
   * Build a data line into the appropriate class object
   *
   * @param {string} data The data to be parsed
   *
   * @return {Object|boolean}
   */
  static Build(data) {
    if (LooksLike.ShadingScheme(data)) {
      return HorizontalShadingScheme.Build(data);
    }

    if (LooksLike.CeilingHeight(data)) {
      return CeilingHeight.Build(data);
    }

    if (LooksLike.WindowConstruction(data)) {
      return WindowConstruction.Build(data);
    }

    if (LooksLike.ZoneWindow(data)) {
      return ZoneWindow.Build(data);
    }

    if (LooksLike.ZoneDetail(data)) {
      return ZoneDetail.Build(data);
    }

    if (LooksLike.FloorConstruction(data)) {
      return FloorConstruction.Build(data);
    }

    if (LooksLike.ZoneFloor(data)) {
      return ZoneFloor.Build(data);
    }

    return false;
  }

}

export default ScratchFileLine;



