/**
 * Break a Window construction data line into its various parts
 */
class WindowConstruction {

  static CONST__ID = 3;
  static CONST__NAME = '';

  static Regex = '^ \\d(.{3})[ \\d]*([a-zA-Z][a-zA-Z\\-\\d]{9}[ a-zA-Z][a-zA-Z]?)';

  id = '';
  name = '';

  constructor(lineData) {
    const regex = new RegExp(WindowConstruction.Regex);
    const data = regex.exec(lineData);

    this.id = data[1].trim();
    this.name = data[2].trim();
  }

  toObject() {
    return {
      id: this.id,
      name: this.name
    };
  }

  static Build(data) {
    return new WindowConstruction(data);
  }

}

export default WindowConstruction;



