/**
 * Class ZoneDetail
 */
class ZoneDetail {

  static Regex = '^C Z00?(\\d{1,3}) => (.*)$';

  id = '';
  name = '';

  constructor(lineData) {
    const regex = new RegExp(ZoneDetail.Regex);
    const data = regex.exec(lineData);

    this.id = data[1].trim();
    this.name = data[2].trim();
  }

  getId() {
    return this.id;
  }

  getName() {
    return this.name;
  }

  toObject() {
    return {
      id: this.getId(),
      name: this.getName()
    };
  }

  static Build(data) {
    return new ZoneDetail(data);
  }

}

export default ZoneDetail;



