/**
 * Class to parse window information inside a zone section
 */
class ZoneWindow {

  static CONST__ZONE_ID = 1;
  static CONST__WINDOW_ID = 2;
  static CONST__HEIGHT = 6;
  static CONST__WIDTH = 7;
  static CONST__AZIMUTH = 8;
  static CONST__HORZ_SHADING_1 = 9;
  static CONST__HORZ_SHADING_2 = 10;

  static Regex = '^ 3(.{3})(( (10)|(  [0-9])))(.{6})(.{6})(.{6})(.{6})(.{6})(.{6})(.{6})(.{6})(.{6})(.{6})(.{6})';

  zoneId = '';
  windowId = '';
  height = '';
  width = '';
  azimuth = '';
  headHeight = ''; // This value isn't used, just here to it can hold an empty cell in the output CSV
  horizShading1Id = '';
  horizShading2Id = '';

  constructor(lineData) {
    const regex = new RegExp(ZoneWindow.Regex);
    const data = regex.exec(lineData);

    this.zoneId = data[ZoneWindow.CONST__ZONE_ID].trim();
    this.windowId = data[ZoneWindow.CONST__WINDOW_ID].trim();
    this.height = data[ZoneWindow.CONST__HEIGHT].trim();
    this.width = data[ZoneWindow.CONST__WIDTH].trim();
    this.azimuth = data[ZoneWindow.CONST__AZIMUTH].trim();
    this.horizShading1Id = data[ZoneWindow.CONST__HORZ_SHADING_1].trim();
    this.horizShading2Id = data[ZoneWindow.CONST__HORZ_SHADING_2].trim();
  }

  getZoneId() {
    return this.zoneId;
  }

  getWindowId() {
    return this.windowId;
  }

  getHeight() {
    return this.height;
  }

  getWidth() {
    return this.width;
  }

  getAzimuth() {
    return this.azimuth;
  }

  getHeadHeight() {
    return this.headHeight;
  }

  getHorizShading1Id() {
    return this.horizShading1Id;
  }

  getHorizShading2Id() {
    return this.horizShading2Id;
  }

  toObject() {
    return {
      zoneId: this.getZoneId(),
      windowId: this.getWindowId(),
      height: this.getHeight(),
      width: this.getWidth(),
      azimuth: this.getAzimuth(),
      headHeight: this.getHeadHeight(),
      horizShading1Id: this.getHorizShading1Id(),
      horizShading2Id: this.getHorizShading2Id()
    };
  }

  static Build(data) {
    return new ZoneWindow(data);
  }

}

export default ZoneWindow;



