import FloorConstruction from './FloorConstruction';
/**
 * Class ZoneFloor
 */
class ZoneFloor {

  static Regex = ` 3(...)(${FloorConstruction.ConstructionIdRegex})`;

  zoneId = '';
  constructionId = '';

  constructor(lineData) {
    const regex = new RegExp(ZoneFloor.Regex);
    const data = regex.exec(lineData);

    this.zoneId = data[1].trim();
    this.constructionId = data[2].trim();
  }

  getZoneId() {
    return this.zoneId;
  }

  getConstructionId() {
    return this.constructionId;
  }

  toObject() {
    return {
      zoneId: this.getZoneId(),
      constructionId: this.getConstructionId()
    };
  }

  static Build(data) {
    return new ZoneFloor(data);
  }

}

export default ZoneFloor;



