import Orientation from './Orientation';
import GroundFloor from './GroundFloor';
import { flatten } from './utils';
/**
 * Class ResultsBuilder
 *
 * This class will take various arrays of data lines, build them into their appropriate classes,
 * link them through various ID's, and then create multiple output lines
 */

class ResultsBuilder {

  /**
   * Compile the various arrays into a single array
   *
   * @param {array} windowConstructions
   * @param {array} zoneWindows
   * @param {array} shading
   * @param {array} ceilingHeights
   * @param {array} zoneDetails
   * @param {array} floorConstructions
   * @param {array} zoneFloors
   *
   * @returns {array}
   *
   * @constructor
   */
  static Compile({
    windowConstructions, zoneWindows, shading, ceilingHeights, zoneDetails, floorConstructions, zoneFloors
  }) {
    return zoneDetails.map((zone) => {
      // Filter the zone windows for only windows in this zone, and then map the window construction, shading and ceiling height details
      const windows = zoneWindows.filter(window => window.zoneId === zone.id).map((zoneWindow) => {
        const construction = windowConstructions.filter(cons => cons.id === zoneWindow.getWindowId())[0];
        const shadingScheme = shading.filter(shade => shade.getId() === zoneWindow.getHorizShading1Id() || shade.getId() === zoneWindow.getHorizShading2Id());
        const ceilingHeight = ceilingHeights.filter(ceiling => ceiling.getZoneId() === zone.getId())[0];

        return {
          ...zoneWindow,
          construction,
          shading: shadingScheme,
          ceilingHeight
        };
      });

      const floors = zoneFloors.filter(zoneFloor => zoneFloor.zoneId === zone.id).map((zoneFloor) => {
        const construction = floorConstructions.filter(floorCon => floorCon.id === zoneFloor.constructionId)[0];

        return {
          ...zoneFloor,
          ...construction
        };
      });

      return {
        id: zone.getId(),
        name: zone.getName(),
        windows,
        onGroundFloor: GroundFloor.Test(floors)
      };
    });
  }

  /**
   * Build the zones array into a flatt array ready for conversion to CSV
   *
   * @param {array}  zones     The zones array created in ResultsBuilder.Compile()
   * @param {string} reference [Optional] Reference North azimuth. Default 0
   *
   * @returns {array}
   */
  static Build(zones, reference = 0) {
    const csv = zones.map((zone) => {
      const windowCsv = zone.windows.map((window) => {
        let projection = '';
        let eaveOffset = '';
        let pergolaOffset = '';
        if (window.shading.length > 0) {
          projection = window.shading[0].projection;
          eaveOffset = window.shading[0].eaveOffset;
          pergolaOffset = window.shading[0].pergolaOffset;
        }

        return [
          zone.name,
          '',
          Orientation.Get(window.azimuth, reference),
          window.height,
          window.width,
          projection,
          window.headHeight,
          window.construction.name,
          window.ceilingHeight.height,
          eaveOffset,
          pergolaOffset,
          zone.onGroundFloor ? '0' : ''
        ];
      });

      // An empty windowCsv needs to be returned inside an array itself, in order to work with the flatten function
      return windowCsv.length > 0 ? windowCsv : [windowCsv];
    });

    return flatten(csv);
  }

}

export default ResultsBuilder;



