/**
 * Class FloorConstruction
 */
class FloorConstruction {

  static ConstructionIdRegex = '(24[1-9]|2[5-9]\\d|3[1-3]\\d|340)';
  static Regex = `^ 2${FloorConstruction.ConstructionIdRegex}`;
  static CSOGRegex = '^ 2(24[1-9]|2[5-9]\\d|3[1-3]\\d|340)   (.{7})*( 18 100)';

  id = '';
  isCsog = false;

  constructor(lineData) {
    const regex = new RegExp(FloorConstruction.Regex);
    const data = regex.exec(lineData);

    this.id = data[1].trim();
    this.isCsog = FloorConstruction.TestCsog(lineData);
  }

  /**
   * Get the construction id that this floor construction belongs to
   *
   * @returns {string}
   */
  getId() {
    return this.id;
  }

  /**
   * Return true/false indicating if the flooring type in the zone is CSOG
   *
   * @returns {boolean}
   */
  getIsCsog() {
    return this.isCsog;
  }

  /**
   * Test the data line to see if there is concrete
   * @returns {boolean}
   */
  static TestCsog(lineData) {
    const csogRegex = new RegExp(FloorConstruction.CSOGRegex);
    return csogRegex.test(lineData);
  }

  static Build(data) {
    return new FloorConstruction(data);
  }

  toObject() {
    return {
      id: this.getConstructionId(),
      isCsog: this.getIsCsog()
    };
  }

}

export default FloorConstruction;



